import { createAction, nanoid } from '@reduxjs/toolkit';

export const addContact = createAction('phonebook/add', ({ id = nanoid(), name, number }) => {
  return {
    payload: {
      id,
      name,
      number,
    },
  };
});

export const deleteContact = createAction('phonebook/delete', id => {
  return {
    payload: { id },
  };
});
